import { useEffect, useMemo, useState } from "react";
import { Badge, Button, Form, Modal } from "react-bootstrap";
import { useGarageContext } from "../Garage";

const VehicleTransfer = ({ vehicle, show, onHide }) => {
  const { plate, garage_id: vehGarageId, blacklisted } = vehicle;
  const {
    config,
    allGarages,
    allPlayers,
    garageId,
    garageVariant,
    financeData,
    locale,
  } = useGarageContext();

  const validGarages = useMemo(
    () => allGarages.filter((g) => vehGarageId !== g),
    [vehGarageId, allGarages]
  );
  const vehicleFinanceData = financeData.vehicles?.find(
    ({ plate }) => plate === vehicle.plate
  );

  const [transferType, setTransferType] = useState(
    config.EnableTransfers.betweenGarages ? "garage" : "player"
  );
  const [selectedGarage, setSelectedGarage] = useState(
    validGarages.find((g) => g === garageId) || validGarages[0]
  );
  const [selectedPlayer, setSelectedPlayer] = useState(allPlayers[0].id);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    setSelectedGarage(
      validGarages.find((g) => g === garageId) || validGarages[0]
    );
    setSelectedPlayer(allPlayers[0].id);
  }, [show, validGarages, garageId, allPlayers]);

  const onTransfer = async (e) => {
    e.preventDefault();
    setLoading(true);

    await fetch(`https://jg-advancedgarages/garageTransferVehicle`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json; charset=UTF-8",
      },
      body: JSON.stringify({
        plate,
        type: transferType,
        playerId: parseInt(selectedPlayer),
        garageId: selectedGarage,
        curGarageId: garageId,
        houseGarage: garageVariant === "house",
      }),
    });

    setLoading(false);
    onHide();
  };

  if (
    !config.EnableTransfers.betweenGarages &&
    !config.EnableTransfers.betweenPlayers
  )
    return null;

  return (
    <Modal show={show} onHide={onHide} centered style={{ zIndex: 9999 }}>
      <Form onSubmit={onTransfer}>
        <Modal.Header closeButton>{locale.vehicleTransfer}</Modal.Header>
        <Modal.Body>
          <div className="d-flex align-items-center">
            <Form.Select
              className="me-1 w-25 flex-shrink-0"
              value={transferType}
              onChange={(e) => setTransferType(e.target.value)}
              required
            >
              {config.EnableTransfers.betweenGarages && (
                <option value="garage">{locale.garage}</option>
              )}
              {config.EnableTransfers.betweenPlayers &&
                !blacklisted &&
                !vehicleFinanceData && (
                  <option value="player">{locale.player}</option>
                )}
            </Form.Select>

            <div className="flex-fill">
              {transferType === "garage" && (
                <Form.Select
                  value={selectedGarage}
                  onChange={(e) => setSelectedGarage(e.target.value)}
                  required
                >
                  {!validGarages.length ? (
                    <option disabled>{locale.noAvailableGarages}</option>
                  ) : (
                    validGarages.map((g) => (
                      <option value={g} key={g}>
                        {g} {garageId === g && `(${locale.currentGarage})`}
                      </option>
                    ))
                  )}
                </Form.Select>
              )}

              {transferType === "player" && (
                <Form.Select
                  value={selectedPlayer}
                  onChange={(e) => setSelectedPlayer(e.target.value)}
                  required
                >
                  {!allPlayers.length ? (
                    <option disabled>{locale.noPlayersOnline}</option>
                  ) : (
                    allPlayers.map(({ id, name }) => (
                      <option value={id} key={id}>
                        {!config.TransferHidePlayerNames
                          ? `${name} (#${id})`
                          : `ID: ${id}`}
                      </option>
                    ))
                  )}
                </Form.Select>
              )}
            </div>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button
            variant="primary"
            className="ms-1 flex-shrink-0"
            type="submit"
            disabled={loading}
          >
            {locale.vehicleTransfer}
            {config.GarageVehicleTransferCost > 0 && (
              <Badge bg="white" text="primary" className="ms-1">
                {transferType === "garage" &&
                  config.GarageVehicleTransferCost > 0 &&
                  `${config.CurrencySymbol}${config.GarageVehicleTransferCost}`}
              </Badge>
            )}
          </Button>
        </Modal.Footer>
      </Form>
    </Modal>
  );
};

export default VehicleTransfer;
