import { Accordion, Badge, Button } from "react-bootstrap";
import { useGarageContext } from "../Garage";
import { useState } from "react";
import { useAppContext } from "../../../AppContext";

const VehicleHeader = ({ vehicle }) => {
  const {
    spawnCode,
    vehicleLabel,
    plate,
    impound,
    garage_id: vehGarageId,
    mileage,
    needsServicing,
  } = vehicle;

  const { event } = useAppContext();
  const { garageId, garageType, garageVariant, locale, financeData, config } =
    useGarageContext();
  const [editingName, setEditingName] = useState(false);
  const [nickname, setNickname] = useState(vehicle.nickname || "");
  const vehicleFinanceData = financeData.vehicles?.find(
    ({ plate }) => plate === vehicle.plate
  );

  const onEditNickname = async (e) => {
    e.stopPropagation();

    if (editingName) {
      setEditingName(false);
      await fetch(`https://jg-advancedgarages/vehicle-set-nickname`, {
        method: "POST",
        headers: {
          "Content-Type": "application/json; charset=UTF-8",
        },
        body: JSON.stringify({
          plate: vehicle.plate,
          nickname,
        }),
      });
      return;
    }

    setEditingName(true);
  };

  return (
    <Accordion.Header>
      <div className="d-flex gap-2 flex-fill align-items-center">
        {config.ShowVehicleImages && (
          <div
            className="garage-row-image"
            style={{
              backgroundImage: `
                url(https://cfx-nui-jg-advancedgarages/vehicle_images/${spawnCode}.png),
                url(https://cfx-nui-jg-dealerships/vehicle_images/${spawnCode}.png),
                url(https://docs.fivem.net/vehicles/${spawnCode}.webp)
              `,
            }}
          />
        )}
        <div className="flex-1 d-flex flex-fill justify-content-between align-items-center vehicle-acc-header">
          <div>
            <div className="d-flex align-items-center">
              {editingName ? (
                <input
                  onClick={(e) => e.stopPropagation()}
                  value={nickname}
                  onChange={(e) => {
                    e.preventDefault();
                    setNickname(e.target.value);
                  }}
                  onKeyDown={(e) => {
                    e.stopPropagation();
                    if (e.key === "Enter") onEditNickname(e);
                    if (e.key === "Escape") setEditingName(false);
                  }}
                  className="edit-vehicle-name-input"
                  autoFocus
                  placeholder={vehicleLabel}
                  maxLength={50}
                />
              ) : (
                <h6 className="m-0">{nickname || vehicleLabel}</h6>
              )}
              {plate && vehGarageId && (
                <Button
                  variant="link"
                  size="sm"
                  className={!editingName && `edit-vehicle-name`}
                  onClick={onEditNickname}
                >
                  <i
                    className={
                      editingName ? "bi-check-circle-fill" : "bi-pencil-fill"
                    }
                  />
                </Button>
              )}
            </div>
            {plate && (
              <div className="d-flex gap-2 mt-1">
                <Badge
                  bg="black"
                  text="warning"
                  style={{ fontFamily: "monospace" }}
                >
                  {plate}
                </Badge>

                {event.jgMileageStarted &&
                  garageType === "car" &&
                  typeof mileage === "number" && (
                    <Badge bg="none" text="muted">
                      <i className="bi-speedometer2 me-1" />
                      {Math.floor(
                        mileage *
                          (config.MileageUnit === "miles" ? 0.621371 : 1)
                      )}
                      {config.MileageUnit === "miles" ? " mi" : " km"}
                    </Badge>
                  )}

                {needsServicing && (
                  <Badge bg="none" text="danger" className="me-2">
                    <i className="bi-exclamation-triangle-fill me-1" />
                    {locale.vehicleNeedsService}
                  </Badge>
                )}

                {financeData && vehicleFinanceData && (
                  <Badge
                    bg="none"
                    text={
                      vehicleFinanceData.finance_data.payment_failed
                        ? "danger"
                        : "success"
                    }
                    className="me-2"
                  >
                    {vehicleFinanceData.finance_data.payment_failed ? (
                      <>
                        <i className="bi-exclamation-triangle-fill me-1" />
                        {financeData.locale.failed}
                      </>
                    ) : (
                      <>
                        <i className="bi-check-circle-fill me-1" />
                        {financeData.locale.activeFinance}
                      </>
                    )}
                  </Badge>
                )}
              </div>
            )}
          </div>
          <div className="me-3">
            {impound ? (
              <Badge bg="danger">
                <i className="bi-exclamation-triangle-fill me-1" />
                {locale.impound}
              </Badge>
            ) : (
              ["public", "house"].includes(garageVariant) && (
                <Badge
                  bg={vehGarageId === garageId ? "primary" : "none"}
                  text={vehGarageId === garageId ? "white" : "muted"}
                >
                  <i className="bi-geo-alt-fill me-1" />
                  {vehGarageId}
                </Badge>
              )
            )}
          </div>
        </div>
      </div>
    </Accordion.Header>
  );
};

export default VehicleHeader;
